import React from 'react';

//imports
import { Grommet } from 'grommet';
import { Routes, Route, Outlet, Link } from "react-router-dom";

//screens
import { HomeScreen } from './screens/home/home'
import { TicketList } from './screens/ticketslist/ticketlist'

function App() {
  const theme = {
    global: {
      font: {
        family: 'Roboto',
        size: '10px',
        height: '20px',
      },
    },
  };



  return (
    <Grommet theme={theme}>

      <Routes>
        <Route path="/" element={<HomeScreen />} />
        <Route path="pasajes/" element={<TicketList />} />









        {/* Esse link redireciona pra tela inicial caso algum usuario conecta a um link inexistente */}
        <Route path="*" element={<HomeScreen />} />

      </Routes>



    </Grommet>
  );
}

export default App;
